import Container from "components/admin/common/Container";
import List from "components/admin/common/List";
import Name from "components/admin/common/Name";
import Order from "components/admin/common/Order";
import Ulist from "components/admin/common/Ulist";
import Title from "components/admin/Title";
// 모달
import ExitButton from "components/auth/mypage/questlist/modal/ExitButton";
import QContainer from "components/auth/mypage/questlist/modal/QContainer";
import QTitle from "components/auth/mypage/questlist/modal/QTitle";
import QWrapper from "components/auth/mypage/questlist/modal/QWrapper";
import Tag from "components/auth/mypage/questlist/modal/Tag";

import { allUserInfo, dropUser } from "controllers/admin";
import { useCallback, useEffect, useState } from "react";

const User = () => {
  const [open, setOpen] = useState<boolean>(false);
  const [num, setNum] = useState<number>(0);
  const [result, setResult] = useState<any | any[]>([]);
  const [item, setItem] = useState<any | any[]>([]);
  const [isLoading, setIsLoading] = useState<boolean>(true);

  const onClick = () => {
    setOpen(false);
  }

  const fetchMoreData = async() => {
    setIsLoading(true);
    setResult(result.concat(item.slice(0,20)));
    setItem(item.slice(20));
    setIsLoading(false);
  }

  const _infiniteScroll = useCallback(() => {

    let scrollHeight = Math.max(document.documentElement.scrollHeight, document.body.scrollHeight);

    let scrollTop = Math.max(document.documentElement.scrollTop, document.body.scrollTop);

    let clientHeight = document.documentElement.clientHeight;
    scrollHeight-=100;
    if(scrollTop + clientHeight >= scrollHeight && isLoading === false) {  
      fetchMoreData();
    }
  },[isLoading]);

  const getFetchData = async() => {
    let response;
    response = await allUserInfo();
    if(response) {
      setResult(response.slice(0,20));
      response = response.slice(20);
      setItem(response);
    }
    setIsLoading(false);
  }

  useEffect(() => {
    getFetchData();
  }, []);

  useEffect(() => {
    window.addEventListener('scroll', _infiniteScroll, true);
    return () => window.removeEventListener('scroll', _infiniteScroll, true);
  }, [_infiniteScroll]);

  return (
    <>
    <Title>회원관리</Title>
    {
      isLoading ? <div>로딩 중 입니다 :)</div>
                : (
                  <Container>
                    <Ulist>
                      {
                        result.map((res:any, idx:number) => (
                          <List key={idx} onClick={() => {
                            setOpen(true);
                            setNum(idx);
                          }}>
                            <Order>{idx+1}</Order>
                            <Name>{res.user_id}</Name>
                          </List>
                        ))
                      }
                    </Ulist>
                  </Container>
                )
    }
    {
      open && (
        <QWrapper>
          <QContainer>
            <ExitButton src="/images/icons/Exit.png" alt="닫기" onClick={() => setOpen(false)}></ExitButton>
            <QTitle>아이디  {result[num].user_id}</QTitle>
            <QTitle>약관동의  {result[num].selected}</QTitle>
            <QTitle>이름 <br/><br/> {result[num].name}</QTitle>
            <QTitle>북마크</QTitle>
            <QTitle>
            {
              result[num].bookmark.map((res:any) => (
                <Tag key={res._id}>{res.title} </Tag>
              ))
            }
            </QTitle>
            <QTitle>자기소개<br/><br/> {result[num].introduce}</QTitle>
            <QTitle>관심분야<br/><br/> {result[num].interest}</QTitle>
          </QContainer>
        </QWrapper>
      )
    }
  </>
    
  )
}

export default User;