import Fetch from 'controllers/fetch';

export const requestUs = (file:FormData) => {

  return Fetch("/api/v1/user/question", "POST", file)
      .then(res => {
        return res.success;
      })
      .catch(err => {
        return err;
      })
}

export const userQuestList = () => {
  return Fetch("/api/v1/user/question", "GET")
      .then( res => {
        return res.results;
      })
      .catch( err => {
        return err;
      })
}

export const fileDownload = (filename:string) => {
  return Fetch("/api/v1/user/file", "GET", filename)
    .then( res => {
      return res;
    })
    .catch( err => {
      return Promise.reject(err);
    })
}