import Container from "components/auth/mypage/questlist/Container";
import List from "components/auth/mypage/questlist/List";
import Title from "components/auth/mypage/questlist/Title";
import { fileDownload, userQuestList } from "controllers/quest";
import { useEffect, useRef, useState } from "react";
import Date from "components/auth/mypage/questlist/Date";
import QContainer from "components/auth/mypage/questlist/modal/QContainer";
import ExitButton from "components/auth/mypage/questlist/modal/ExitButton";
import QWrapper from "components/auth/mypage/questlist/modal/QWrapper";
import Text from "components/auth/mypage/questlist/modal/Text";
import QTitle from "components/auth/mypage/questlist/modal/QTitle";
import Email from "components/auth/mypage/questlist/modal/Email";
import File from "components/auth/mypage/quest/File";
import QSubTitle from "components/auth/mypage/questlist/modal/QSubTitle";
import QSubWrapper from "components/auth/mypage/questlist/modal/QSubWrapper";
import Loading from "pages/LoadingPage";

const QuestList = () => {
  let res;
  const [result, setResult] = useState<any|any[]>([]);
  const [open, setOpen] = useState<boolean>(false);
  const [num, setNum] = useState<number>(0);
  const ref = useRef<HTMLDivElement>(null);

  useEffect(() => {
    getQuestList();
  },[]);

  const getQuestList = async() => {
    res = await userQuestList();
    console.log(res);
    setResult(res);
  }

  const onMouseDown = (e:any) => {
    if(!ref.current) return ;

    if(!e.path.includes(ref.current)) {
      setOpen(false);
    }
  }

  useEffect(() => {
    if(open) {
      window.addEventListener('mousedown', onMouseDown, true);
    }
    return () => {
      window.removeEventListener("mousedown", onMouseDown, true);
    }
  },[open]);

  return (
    <>
    {
      result ? (
        <Container>
        {
            result.map((data:any, idx:number) => (
              <List key={data._id} onClick={() => {setOpen(true); setNum(idx)}}>
                <Title>{data.text}</Title>
                <Date>2021/01/29</Date>
              </List>
            ))
        }
       </Container>
      )
            : (
              <Loading />
            )
    }
    {
      open && (
        <QWrapper>
          <QContainer ref={ref}>
            <ExitButton src="/images/icons/Exit.png" alt="닫기" onClick={() => setOpen(false)}></ExitButton>
            <QTitle>{result[num].title}</QTitle>
            <QSubTitle>이메일</QSubTitle>
            <Email>{result[num].email}</Email>
            <QSubWrapper>
              <QSubTitle>내용</QSubTitle>
              <QSubTitle>{result[num].date.substring(0, 10)}</QSubTitle>
            </QSubWrapper>
            <Text>{result[num].text}</Text>
            {
             result[num].file !== undefined && result[num].file.file.map((res:any, idx:number) => (
                <File filename={res.originalname} />
              ))
            }
          </QContainer>
        </QWrapper>
      )
    }
    </>
  )
}

export default QuestList;