import { fileDownload } from 'controllers/quest';
import { useEffect, useState } from 'react';
import styled from 'styled-components';

const File = ({filename}:IFile) => {
  const [url, setUrl] = useState<any>("");

  const getUrl = async() => {
    
    const res = await fileDownload(filename);
    console.log(res);
    // setUrl(res);
  }

  useEffect(() => {
    getUrl();
  },[]);


  return (
    <FileTag href={url} download>{filename}</FileTag>
  )
}

const FileTag = styled.a`
  display: inline-block;
  width: 100%;
  font-size: 12px;
  color: black;
`;

interface IFile {
  filename : string;
}

export default File;